'use server'

import {
  FC
} from 'react'

import {
  getDispenserData
} from '@/utils'

import type { InferGetServerSidePropsType, GetServerSideProps } from 'next'


async function Scan (
  {
    params
  }: {
    params: {
      multiscan_qr_id: string,
      scan_id: string,
      scan_id_sig: string,
      multiscan_qr_enc_code: string
    }
  }
) {

  const redirectUrl  = await getDispenserData(
    multiscan_qr_id,
    multiscan_qr_enc_code,
    
  )


  return null
}

export const getServerSideProps = (async (context) => {
  const UA = context.req.headers['user-agent']

  if (UA) {
    const isMobile = Boolean(UA.match(
      /Android|BlackBerry|iPhone|iPad|iPod|Opera Mini|IEMobile|WPDesktop/i
    ))
    
    return {
      props: {
        isMobile
      }
    }
  }

  return {
    props: {
      isMobile: false
    }
  }
  
}) satisfies GetServerSideProps<{}>




export default Scan

